.formDiv {
  width: 75%;
  background-color: #f4f4f4;
}
.formDiv form label {
  display: block;
}
.formDiv select {
  width: 40%;
  font-family: "Poppins";
  padding: 10px 20px;
  border: none;
  margin-bottom: 3%;
}
.formDiv label {
  margin-bottom: 15px;
}
.formDiv h2 {
  margin-bottom: 3%;
}
#selections {
  background-color: white;
  padding: 2%;
  width: 50%;
  margin: 2% 0;
  box-shadow: 0px 5px 5px 2px rgba(0, 0, 0, 0.04);
}
#selections h3 {
  font-weight: 600;
  font-size: 1em;
}
#selections.thank-you {
  background-color: #7bbfc4;
  color: black;
}
#selections.error{
  background-color:pink;
}
@media screen and (max-width: 1024px) {
  .formDiv,
  form {
    width: 100%;
  }
  #selections {
    width: 100%;
    padding: 4%;
    font-size: 1em;
  }
}
@media screen and (max-width: 768px) {
  .formDiv select {
    width: 100%;
  }
  #selections {
    padding:4%;
    font-size: 0.9em;
  }
}
